@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;700&display=swap);
.header{padding:20px;box-sizing:border-box;font-weight:800;font-size:14px;z-index:999;display:flex;align-items:center;justify-content:flex-end}.header div{z-index:999;text-align:right;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;cursor:pointer;-webkit-tap-highlight-color:transparent}.header span{color:#0067F9}.header a{color:#000;text-decoration:none}

.bar-divver{position:fixed;flex-direction:column;bottom:0;left:-100vw;right:0;width:300vw;height:auto;display:flex;align-items:center;justify-content:center;z-index:0}.bar-divver p{cursor:pointer;z-index:4;margin-top:4px;color:#bbc7d8;font-size:12px;text-decoration:underline}@-webkit-keyframes spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(359deg);transform:rotate(359deg)}}@keyframes spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(359deg);transform:rotate(359deg)}}.Bar{z-index:3;background-color:#0067F9;transition:800ms;height:130px;width:300vw;-webkit-transform:rotate(0deg);transform:rotate(0deg);-webkit-transform-origin:center;transform-origin:center;border-top-right-radius:65px;border-bottom-right-radius:65px;box-sizing:border-box;display:flex;align-items:center;justify-content:flex-end;margin-bottom:50vh}.Bar .loader{transition:800ms;z-index:100;border:10px solid #ffffff;border-right-color:#ffffff;border-top-color:rgba(255,255,255,0.6);border-radius:100%;margin-right:15px;height:80px;width:80px;-webkit-animation:spin 800ms infinite linear;animation:spin 800ms infinite linear}.Bar-diagonal{-webkit-transform:rotate(30deg);transform:rotate(30deg)}.Bar-setting-1{margin-bottom:100vh}.Bar-setting-2{margin-bottom:100vh}.Bar-setting-3{margin-bottom:100vh}@-webkit-keyframes loading-move{0%{margin-top:0px}100%{margin-top:5px}}@keyframes loading-move{0%{margin-top:0px}100%{margin-top:5px}}.Bar-loading{-webkit-transform:rotate(0deg);transform:rotate(0deg);width:80vw;margin-left:-20vw;-webkit-animation:loading-move 700ms infinite ease-in-out alternate;animation:loading-move 700ms infinite ease-in-out alternate}@-webkit-keyframes success-bar-key{0%{width:80vw;margin-left:-20vw}30%{width:300vw;margin-left:0}100%{width:130px}}@keyframes success-bar-key{0%{width:80vw;margin-left:-20vw}30%{width:300vw;margin-left:0}100%{width:130px}}@-webkit-keyframes success-circle-key{0%{stroke-dashoffset:600;opacity:1}100%{stroke-dashoffset:0;opacity:1}}@keyframes success-circle-key{0%{stroke-dashoffset:600;opacity:1}100%{stroke-dashoffset:0;opacity:1}}@-webkit-keyframes success-check-key{0%{stroke-dashoffset:-124}100%{stroke-dashoffset:0}}@keyframes success-check-key{0%{stroke-dashoffset:-124}100%{stroke-dashoffset:0}}.Bar-success{border-radius:85px;-webkit-transform:rotate(0deg);transform:rotate(0deg);-webkit-animation:success-bar-key 1000ms cubic-bezier(0.62, 0.13, 0, 0.82);animation:success-bar-key 1000ms cubic-bezier(0.62, 0.13, 0, 0.82);-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.Bar-success .loader{border-color:#fff}.Bar-success svg polyline{stroke-dasharray:124;stroke-dashoffset:-124;-webkit-animation:success-check-key 250ms 1200ms cubic-bezier(1, 0.16, 0, 0.93);animation:success-check-key 250ms 1200ms cubic-bezier(1, 0.16, 0, 0.93);-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.Bar-success svg circle{opacity:0;stroke-dasharray:600;stroke-dashoffset:600;-webkit-transform-origin:center;transform-origin:center;-webkit-transform:rotate(327deg);transform:rotate(327deg);-webkit-animation:success-circle-key 400ms 1650ms cubic-bezier(0.8, 0.28, 0.2, 0.76);animation:success-circle-key 400ms 1650ms cubic-bezier(0.8, 0.28, 0.2, 0.76);-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}@-webkit-keyframes success-loader-disappear{0%{opacity:1;display:block}100%{opacity:0;display:none}}@keyframes success-loader-disappear{0%{opacity:1;display:block}100%{opacity:0;display:none}}.loader-disappear{-webkit-animation:success-loader-disappear 500ms;animation:success-loader-disappear 500ms;display:none;opacity:0}

.main{position:relative}.bottoms{position:fixed;width:100%;bottom:0;padding:20px;box-sizing:border-box}.bottoms p{font-size:14px;color:#0067F9;font-weight:800}.bottoms h1{word-break:keep-all;font-size:24px}.bottoms .btns input[type=text]{margin-top:12px;border:0;border-bottom:solid 1px #CDD5E1;display:block;width:100%;outline:none;box-sizing:border-box;padding:4px;font-size:24px;font-weight:800}.bottoms .btns input[type=text]::-webkit-input-placeholder{color:#CDD5E1}.bottoms .btns input[type=text]:-ms-input-placeholder{color:#CDD5E1}.bottoms .btns input[type=text]::placeholder{color:#CDD5E1}.bottoms .main-btn{word-break:keep-all;cursor:pointer;box-sizing:border-box;margin-top:24px;background-color:#0067F9;border-radius:8px;padding:12px 8px;font-weight:800;font-size:14px;text-align:center;color:#ffffff;width:100%;-webkit-tap-highlight-color:transparent;transition:100ms}.bottoms .main-btn:active{background-color:#0059d4}.bottoms .disable{cursor:default;background-color:#CDD5E1}.bottoms .disable:active{background-color:#CDD5E1}

*{margin:0;padding:0;font-family:'Noto Sans KR', sans-serif}body,html{height:100%}

